const FinanceOtherMiscellaneous = ({ instituteId, fid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const [disabled, setDisabled] = useState(false);
  const [step, setStep] = useState("");

  const [state, setState] = useState({
    amount: "",
    mode: "Offline",
    fee_payment_mode: "Cash",
    fee_payment_amount: "",
    transaction_date: dayjs().format("DD/MM/YYYY"),
    fee_bank_name: "",
    fee_bank_holder: "",
    fee_utr_reference: "",
    grno: "",
    class: "",
    fee_type: "",
    roll_no: "",
    caste_category: "",
    student_name: "",
    applicable_os_pending: "",
  });
  const [errorField, setErrorField] = useState("");
  const [singleStudent, setSingleStudent] = useState(null);
  const [miscellaneousFee, setMiscellaneousFee] = useState(null);
  const [responseData, setResponseData] = useState(null);
  const [snackbar, setSnackbar] = useState({
    open: false,
    msg: "",
    variant: "error",
  });
  const [openPdf, setOpenPdf] = useState("");

  const [pgstate, setPgstate] = useState({
    amount: 0,
    type: "Other",
    isTransaction: false,
  });

  const [miscellanesouCombinedCollectFees] =
    useMiscellanesouCombinedCollectFees();

  const onNavigate = useCallback(() => {
    let url = "";
    url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/institute/member/finance`
        : `/q/${params.username}/member/staff/finance`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);

  useEffect(() => {
    if (getQuery.state?.feesId) {
      setMiscellaneousFee({
        _id: getQuery.state?.feesId,
        bank_account: {
          finance_bank_account_name: getQuery.state?.feesBankName,
        },
        other_fees_name: getQuery.state?.feesName,
      });
      setState((prev) => ({
        ...prev,
        fee_type: getQuery.state?.feeType ?? "",
        amount: getQuery.state?.otherData?.payable_amount ?? 0,
        fee_payment_amount: getQuery.state?.otherData?.payable_amount ?? 0,
      }));
    }
  }, [getQuery.state?.feesId]);

  const onDeleteMenu = () => {
    if (responseData?.receipt) {
      setStep(3);
    } else {
      setSnackbar({
        open: true,
        msg: "No Receipt created.",
        variant: "error",
      });
    }
  };
  const onCollectFees = () => {
    let validation = requiredField(state, [
      "mode",
      "fee_payment_amount",
      "fee_bank_name",
      "fee_bank_holder",
      "fee_utr_reference",
      "grno",
      "class",
      "fee_type",
      "roll_no",
      "caste_category",
      "applicable_os_pending",
    ]);
    if (
      existingRequiredField(validation) &&
      fid &&
      singleStudent?._id &&
      miscellaneousFee?._id
    ) {
      setDisabled((pre) => !pre);
      miscellanesouCombinedCollectFees({
        fid: fid,
        bodyParameter: {
          amount: state?.amount ?? 0,
          mode:
            state?.fee_payment_mode === "Cash" ? "Offline" : state?.mode ?? "",
          fee_payment_mode:
            state?.fee_payment_mode === "Cash"
              ? "By Cash"
              : state?.fee_payment_mode,
          fee_payment_amount: state?.fee_payment_amount ?? 0,
          transaction_date: yearMonthDay(state.transaction_date) ?? "",
          fee_bank_name: state?.fee_bank_name ?? "",
          fee_bank_holder: state?.fee_bank_holder ?? "",
          fee_utr_reference: state?.fee_utr_reference ?? "",
          staff_id: getQuery.state?.sid ?? "",
          ofid: miscellaneousFee?._id,
          sid: singleStudent?._id,
          grno: state?.grno ?? "",
          class: state?.class ?? "",
          fee_type: state?.fee_type ?? "",
          roll_no: state?.roll_no ?? "",
          caste_category: state?.caste_category ?? "",
          student_name: state.student_name ?? "",
          applicable_os_pending: state.applicable_os_pending ?? 0,
        },
      })
        .then((res) => {
          setTimeout(() => {
            setResponseData(res.data);
            setOpenPdf(res?.data?.key);
            setDisabled((pre) => !pre);
          }, 1500);

          // console.info("dsfs b", res.data);
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };

  const onCleanData = () => {
    setState({
      amount: "",
      mode: "Offline",
      fee_payment_mode: "Cash",
      fee_payment_amount: "",
      transaction_date: dayjs().format("DD/MM/YYYY"),
      fee_bank_name: "",
      fee_bank_holder: "",
      fee_utr_reference: "",
      grno: "",
      class: "",
      fee_type: "",
      roll_no: "",
      caste_category: "",
      student_name: "",
      applicable_os_pending: "",
    });
    setErrorField("");
    setSingleStudent(null);
    if (getQuery.state?.feesId) {
    } else {
      setMiscellaneousFee(null);
    }
    setResponseData(null);
    setStep("");
    setPgstate({
      amount: 0,
      type: "Other",
      isTransaction: false,
    });
  };
  // const onPrintPdf = (e) => {
  //   // e.preventDefault();
  //   // // Send a message to the main window when the button is clicked
  //   // document.getElementById("print-file").addEventListener("click", () => {
  //   //   window.parent.postMessage("Hello from iframe!", "*");
  //   // });

  //   // // Listen for messages from the main window
  //   // window.addEventListener("message", (event) => {
  //   //   console.log("Message from main window:", event.data);
  //   // });

  //   // const node = document.getElementById("print-file");
  //   // node.contentWindow.focus();
  //   // node.contentWindow?.print(`${daybookBaseUrl}/${responseData?.key}`);

  //   // window
  //   //   .print
  //   //   // `${daybookBaseUrl}/${responseData?.key}`
  //   //   // "PRINT",
  //   //   // "height=400,width=600"
  //   //   ();
  // };

  // function printDiv() {
  //   let divContents = document.getElementById("GFG").innerHTML;
  //   let printWindow = window.open("", "", "height=500, width=500");
  //   printWindow.document.open();
  //   printWindow.document.write(`
  //       <html>
  //       <head>
  //           <title>Print Div Content</title>
  //           <style>
  //               body { font-family: Arial, sans-serif; }
  //               h1 { color: #333; }
  //           </style>
  //       </head>
  //       <body>
  //           ${divContents}
  //       </body>
  //       </html>
  //   `);
  //   printWindow.document.close();
  //   printWindow.print();
  // }

  // const onPrintPdf = () => {
  //   if (responseData?.key) {
  //     if (step === 4) {
  //       setStep("");
  //     } else {
  //       setStep(4);
  //     }
  //   }
  // };
  // console.info("asfjbs", getQuery.state);

  return (
    <>
      <UserMemberContainerWrapper
        customStyle={{
          marginTop: "-1rem",
        }}
      >
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <div
              className={style.fm_header_start_container}
              style={{
                gap: "0.2rem",
              }}
            >
              <img
                alt="search icon"
                src={`${assestsUtilityUrl}/back.svg`}
                style={{
                  height: "2rem",
                  width: "2rem",
                }}
                onClick={onNavigate}
              />
              {getQuery.state?.feesId ? (
                <h6>
                  {miscellaneousFee?.other_fees_name ?? ""}
                  {" , "}{" "}
                  {miscellaneousFee?.bank_account?.finance_bank_account_name ??
                    ""}
                </h6>
              ) : (
                <h6>{t("miscellaneous_fees_collect")}</h6>
              )}
              {/* <button onClick={window.print()}>Print this page</button> */}
            </div>
            <div
              className={style.fm_header_start_container}
              style={{
                gap: "1rem",
              }}
            >
              <CreateButton
                label="new_receipt"
                onAction={onCleanData}
                customStyle={{
                  margin: "0",
                  padding: "0.3rem 1rem",
                }}
              />
              {/* <CreateButton
                label="print"
                onAction={onPrintPdf}
                customStyle={{
                  margin: "0",
                  padding: "0.3rem 1rem",
                }}
              /> */}
              <CreateButton
                label="generate"
                onAction={onCollectFees}
                customStyle={{
                  margin: "0",
                  padding: "0.3rem 1rem",
                }}
              />
              <CreateButton
                label="delete_receipt"
                onAction={onDeleteMenu}
                customStyle={{
                  margin: "0",
                  padding: "0.3rem 1rem",
                }}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <HorizontalTabWrapper>
          {tabList.map((tab) => (
            <HorizontalTabCard
              key={tab?.id}
              tab={tab}
              isLink
              isActive={tab.groupIs?.includes(searchParams.get("n"))}
              parentState={getQuery.state}
              url={
                getQuery.state?.urlOffSet === "Institute"
                  ? `/q/${params.username}/institute/member/finance?type=miscellaneous-receipt${tab.urlKey}`
                  : `/q/${params.username}/member/staff/finance?type=miscellaneous-receipt${tab.urlKey}`
              }
            />
          ))}
        </HorizontalTabWrapper>
        <BorderBottom
          customStyleBorder={{
            marginTop: "0",
          }}
        />
        {/* {step === 4 && searchParams.get("n") !== "history" ? (
          <UserMemberContentWrapper>
            {responseData?.key ? (
              <iframe
                src={`${daybookBaseUrl}/${responseData?.key}`}
                style={{
                  width: "100%",
                  height: "calc(100dvh - 80px)",
                }}
                title="Receipt print"
              />
            ) : null}
          </UserMemberContentWrapper>
        ) : ( */}
        <UserMemberContentWrapper>
          {searchParams.get("n") === "history" ? (
            <OtherTransactionHistory fid={fid} instituteId={instituteId} />
          ) : (
            <FinanceOtherMiscellaneousNewReceipt
              instituteId={instituteId}
              fid={fid}
              setState={setState}
              state={state}
              setErrorField={setErrorField}
              errorField={errorField}
              setSingleStudent={setSingleStudent}
              singleStudent={singleStudent}
              setMiscellaneousFee={setMiscellaneousFee}
              miscellaneousFee={miscellaneousFee}
              setPgstate={setPgstate}
              pgstate={pgstate}
              setStep={setStep}
              step={step}
            />
          )}
        </UserMemberContentWrapper>
        {/* )} */}
      </UserMemberContainerWrapper>

      {step === 3 ? (
        <OtherTransactionHistoryCardMenu
          onClose={() => setStep("")}
          frid={responseData?.receipt}
          onRefetch={onCleanData}
          ofid={miscellaneousFee?._id}
        />
      ) : null}
      {disabled && <QvipleLoader />}
      <NotifyEvent eventState={snackbar} eventStateHandler={setSnackbar} />
      {openPdf && (
        <CustomGeneratedPdf
          subUrl={daybookBaseUrl}
          documentUrl={openPdf}
          fileName={openPdf}
          onClose={() => setOpenPdf("")}
        />
      )}
      {/* {responseData?.key ? (
        <div id="GFG">
          <img src={`${daybookBaseUrl}/${responseData?.key}`} alt="sfbsnfs" />
          <iframe
            src={`${daybookBaseUrl}/${responseData?.key}`}
            style={{
              width: "55rem",
              height: "calc(100dvh - 80px)",
            }}
            // title="ducoment-preview"
          />
        </div>
      ) : null} */}

      {/* <object
        data={`${daybookBaseUrl}/${responseData?.key}`}
        // data={`${pdfCdnUrl}/${responseData?.key}`}
        type="application/pdf"
      > */}
      {/* <iframe
        title="pdf document"
        id="print-file"
        // src="http://localhost:3000"
        src={`${daybookBaseUrl}/${responseData?.key}`}
        // src={`${pdfCdnUrl}/${responseData?.key}`}
        // src={`https://docs.google.com/viewer?url=${pdfFile}&embedded=true`}
        // src={`https://docs.google.com/viewer?url=${pdfFile}&embedded=true`}
      /> */}
      {/* </object> */}
    </>
  );
};

export default FinanceOtherMiscellaneous;
const tabList = [
  {
    label: "receipt",
    id: "receipt",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "transaction",
    id: "history",
    urlKey: "&n=history",
    groupIs: ["history"],
  },
];
