import { useTranslation } from "react-i18next";
import CustomSelectMiscellaneousFees from "../../Ui/Select/CustomSelectMiscellaneousFees";
import { useFinanceAllOtherFeesList } from "../../Hooks/ApiHooks/FinanceManager/finance-manager-api";

const InstituteMiscellaneousFeesDorpdown = ({
  fid,
  onSelect,
  errorShow,
  defalutValue,
  isDenied,
  customStyleContainer,
  isFlow,
  customListStyle,
  customLabelStyle,
}) => {
  const { t } = useTranslation();
  const { financeAllOtherFeesList } = useFinanceAllOtherFeesList({
    data: {
      fid: fid,
      page: 1,
      limit: 1000,
      search: "",
      type: "",
      // type: isFlow ?? "",
    },
    skip: !fid,
  });
  return (
    <>
      <CustomSelectMiscellaneousFees
        selectLabel={t("select_miscellaneous_fees")}
        selectedValue={t("select_miscellaneous_fees_placeholder")}
        options={
          financeAllOtherFeesList?.list?.length > 0
            ? financeAllOtherFeesList?.list
            : []
        }
        onClick={onSelect}
        isFull
        errorShow={errorShow}
        defalutValue={defalutValue}
        isDenied={isDenied}
        customStyleContainer={customStyleContainer}
        customListStyle={customListStyle}
        customLabelStyle={customLabelStyle}
      />

      {/* {instituteAllUniversalBatchLoading && <Loading />} */}
    </>
  );
};

export default InstituteMiscellaneousFeesDorpdown;
