  const { t } = useTranslation();
  const getQuery = useLocation();
  const { studentInstituteCharges } = useStudentInstituteCharges({
    data: {
      id: instituteId,
    },
    skip: !instituteId,
  });
  const onCloseBackdrop = () => {
    setStep("");
  };
  const onInputAmount = (e) => {
    setState((pre) => ({
      ...pre,
      amount: +e.target.value,
      fee_payment_amount: +e.target.value,
    }));
    setPgstate((prev) => ({
      ...prev,
      amount: +e.target.value,
    }));
  };

  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onTransactionDate = (val) => {
    setState((prev) => ({
      ...prev,
      transaction_date: slashDateFormat(val),
    }));
  };
  const onModeChange = (val) => {
    setState((prev) => ({
      ...prev,
      mode: "Online",
      fee_payment_mode: val,
    }));
  };

  const onCatseSelect = (val) => {
    setState((prev) => ({
      ...prev,
      caste_category: val ?? "",
    }));
  };

  const onSelectStudent = (dt) => {
    setSingleStudent(dt);
    let nt = dt?.studentFirstName;
    nt += dt?.studentMiddleName ? ` ${dt?.studentMiddleName}` : "";
    nt += dt?.studentLastName ? ` ${dt?.studentLastName}` : "";
    setState((prev) => ({
      ...prev,
      grno: dt?.studentGRNO ?? "",
      class: dt?.studentClass?.classTitle ?? "",
      roll_no: dt?.studentROLLNO ?? "",
      caste_category: dt?.studentCastCategory ?? "",
      student_name: nt,
      applicable_os_pending: dt?.applicable_fees_pending,
    }));
    onCloseBackdrop();
  };

  const onSelectOtherFee = (ot) => {
    setMiscellaneousFee(ot);
    setState((prev) => ({
      ...prev,
      fee_type: ot?.other_fees_type ?? "",
      amount: ot?.payable_amount ?? 0,
      fee_payment_amount: ot?.payable_amount ?? 0,
    }));
    onCloseBackdrop();
  };

